IViewModel);\nconst mainStatistics = computed(() => viewModel.backoffice?.dashboard?.mainStatistics);\n\nfunction gotoUserDetails() {\n    router.push(\"/backoffice/users\");\n}\nfunction gotoOrganizationDetails() {\n    router.push(\"/backoffice/organizations\");\n}\nfunction gotoTagDetails() {\n    router.push(\"/backoffice/tags\");\n}\nfunction gotoRequestsDetails() {\n    router.push(\"/backoffice/userrequests\");\n}\nfunction goToOrgGroupDetails() {\n    router.push(\"/backoffice/organizationgroups\");\n}\nfunction goToServiceDesignerActivityDetails() {\n    router.push(\"/backoffice/servicedesigner/users\");\n}\n</script>\n\n<template>\n    <div class=\"page-title\">\n        <span><i class=\"mdi mdi-chart-line\" style=\"font-weight: 400;font-size: 28px; margin-right: 12px;\"></i>Plateau Studio Analytics</span>\n    </div>\n    <div class=\"row\" style=\"position: relative; margin-left: 0;\">\n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">Users</span>\n                <img src=\"../../../../images/analytics-user.svg\" style=\"float: right;\"/>\n            </div>\n            <div class=\"counts\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">{{ mainStatistics?.userCount }}</p>\n                    <p class=\"count-text\">Total</p>\n                </div>\n                <div>\n                    <p class=\"count-number\">{{ mainStatistics?.licensedUserCount }}</p>\n                    <p class=\"count-text\">Licensed</p>\n                </div>\n            </div>\n            <div>\n                <button class=\"see-detail-btn\" @click=\"gotoUserDetails\">SEE DETAIL</button>\n            </div>\n            </div>\n        </div>\n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">Organizations</span>\n                <img src=\"../../../../images/analytics-org-users.svg\" style=\"float: right;\"/>\n            </div>\n            <div class=\"counts\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">{{ mainStatistics?.organizationCount }}</p>\n                    <p class=\"count-text\">Total</p>\n                </div>\n                <div>\n                    <p class=\"count-number\">{{ mainStatistics?.licensedOrgCount }}</p>\n                    <p class=\"count-text\">Licensed</p>\n                </div>\n            </div>\n            <div>\n                <button class=\"see-detail-btn\" @click=\"gotoOrganizationDetails\">SEE DETAIL</button>\n            </div>\n            </div>\n        </div>\n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">Applications</span>\n                <img src=\"../../../../images/analytics-apps.svg\" style=\"float: right;\"/>\n            </div>\n            <div class=\"counts\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">{{ mainStatistics?.applicationCount }}</p>\n                    <p class=\"count-text\">Total</p>\n                </div>\n                <div>\n                    <p class=\"count-number\">{{ mainStatistics?.licensedAppCount }}</p>\n                    <p class=\"count-text\">Licensed</p>\n                </div>\n            </div>\n            </div>\n        </div>\n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">Licensing Groups</span>\n                <!-- <img src=\"../../../../images/analytics-tags.svg\" style=\"float: right;\"/> -->\n            </div>\n            <div class=\"counts\" style=\"margin-bottom: 116px;\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">{{ mainStatistics?.orgGroupCount }}</p>\n                </div>\n            </div>\n            <div>\n                <button class=\"see-detail-btn\" @click=\"goToOrgGroupDetails\" >SEE DETAIL</button>\n            </div>\n            </div>\n        </div>\n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">Tags</span>\n                <img src=\"../../../../images/analytics-tags.svg\" style=\"float: right;\"/>\n            </div>\n            <div class=\"counts\" style=\"margin-bottom: 116px;\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">{{ mainStatistics?.tagCount }}</p>\n                </div>\n            </div>\n            <div>\n                <button class=\"see-detail-btn\" @click=\"gotoTagDetails\" >SEE DETAIL</button>\n            </div>\n            </div>\n        </div>\n       \n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">Cost</span>\n                <img src=\"../../../../images/analytics-cost.svg\" style=\"float: right;\"/>\n            </div>\n            <div class=\"counts\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">-</p>\n                </div>\n            </div>\n            </div>\n        </div>\n        <div class=\"card-media\">\n            <div class=\"analytic-type\">\n            <div>\n                <span class=\"analytic-type-name\">User Requests</span>\n                <img src=\"../../../../images/analytics-tags.svg\" style=\"float: right;\"/>\n            </div>\n            <div class=\"counts\" style=\"margin-bottom: 116px;\">\n                <div style=\"margin-bottom: 33px;\">\n                    <p class=\"count-number\">{{ mainStatistics?.userRequestCount }}</p>\n   